.loader {
    display: flex; 
    flex: 1 1 auto;
    justify-content: center;
    align-items: center;
}
.loader__dual-ring {
    display: inline-block;
    width: 80px;
    height: 80px;
}
.loader__dual-ring:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #000;
    border-color: #000 transparent #000 transparent;
    animation: loader__dual-ring 1.2s linear infinite;
}
@keyframes loader__dual-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}